<template>
  <div id="box">
    <img src="@/assets/images/my/personal_bg.png" alt="" class="pos_img">
    <!-- <div class="date">
      <div class="month">{{month}}月</div>
      <van-cell :title="year+'年'" arrow-direction="down" is-link @click="showtime=true"/>
    </div> -->
    <div class="info">
      <div class="top">
        <div class="left">
          <img :src="head_pic" class="img">
          <strong class="user_name">{{nickname}}</strong>
        </div>
        <!-- <div class="icon1">一级分销</div> -->
      </div>
      <!-- <div class="yeji">总业绩(元)</div>
      <div class="num">{{num}}</div> -->
      <div class="bottom">
        <div><span>团队：<strong style="color:#FF4C24;">{{count}}</strong>人</span></div>
        <div><span>总业绩(元)：<strong style="color:#FF4C24;">{{num}}</strong></span></div>
      </div>
    </div>
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <div class="list">
        <div class="item" v-for="(item ,index) in trun_list" :key="index">
          <div class="top">
            <img :src="item.head_pic" alt="" class="icon">
            <div class="left">
              <strong class="name">{{item.nickname}}</strong>
              <!-- <span>团队：<strong style="color:#FF4C24;">20</strong>人</span> -->
            </div>
            <div class="bottom">
              <span>个人业绩(元): </span>
              <span style="color:#FF4C24">&nbsp;&nbsp;&nbsp;&nbsp;{{item.shou_number}}</span>
            </div>
          </div>
          <div class="left_r">
            <img src="@/assets/images/my/del.png" alt="" @click="del_team(item.user_id,index)">
            <img src="@/assets/images/my/eid.png" alt="" @click="$router.push({path:'/edit_myteam', query:{id:item.user_id}})">
          </div>
        </div>
      </div>
    </van-list>
  </div>
</template>

<script>
import { getteam,getmy_trun_del } from '@/axios/getData';
import { Cell, DatetimePicker, Popup, List, Dialog, Toast } from "vant";
export default {
  data(){
    return{
      year:new Date().getFullYear(),
      month:new Date().getMonth()+4,
      currentDate: new Date(),
      showtime:false,
      head_pic:'',
      nickname:'',
      num:'',
      count:'',
      trun_list:[],
      page:1,
      pages:'',   // 总数
      loading: false,
      finished: false
    }
  },
  components: {
    vanCell: Cell,
    vanDatetimePicker: DatetimePicker,
    vanPopup: Popup,
    vanList: List
  },
  created() {
    // this.getdata();
  },
  methods:{
    async getdata(){
      if(this.loading){

        var res = await getteam({page:this.page});
        if(this.page ==1){
          this.head_pic = res.data.head_pic
          this.nickname = res.data.nickname
          this.num = res.data.num
          this.count = res.data.count
          this.trun_list = res.data.trun_list
          this.pages = res.data.pages
        } else {
          this.trun_list = this.trun_list.concat(res.data.trun_list)
        }
        this.loading = false;
        this.page+=1
        if(this.pages==this.trun_list.length){
          this.finished = true
        }
        console.log(res.data)
      }
    },
    async del_team(id,index){
      var that = this
      Dialog.alert({
        title: '删除提示',
        message: '删除后不可恢复哦',
        showCancelButton: true
      }).then(() => {
        getmy_trun_del({user_id:id}).then((res)=>{
          if(res.code==200){
            Toast(res.data);
            that.trun_list.splice(index,1)
          }
        })
      }).catch(() => {
        // on cancel
      });
    },
    onLoad(){
      this.getdata();
    },
    formatter(type, value) {
      if (type === 'year') {
        return `${value}年`;
      } else if (type === 'month') {
        return `${value}月`
      }
      return value;
    },
    //  时间弹出层    点击确定
    gettime(value,a,b){
      this.showtime = false
      const d = new Date(value);
      this.year = d.getFullYear();
      this.month = d.getMonth() + 1
      console.log(this.year, this.month);
      
    },
    //  时间弹出层    点击取消
    cancel(){
      this.showtime = false
    },
    //  时间弹出层    时间改变
    change(picker,a,b){
      console.log(picker,a,b);
    }
  },
}
</script>
<style scoped lang="scss">
#box{
  background:#FAFAFA;
  height:100vh;
}
/deep/ .van-cell{
  background: transparent;
  color:#fff;
  width:unset;
}
/deep/ .van-cell__title{
  flex:unset;
  font-size: 14pt;
}
  .pos_img{
    position: absolute;
    top:0;
    left:0;
    width:100%;
    z-index: 0;
  }
  .date{
    position: absolute;
    display: flex;
    align-items: center;
    top:20pt;
    right:0pt;
    z-index: 1;
    color:#fff;
    .month{
      width:45pt;
      font-size: 20pt;
      font-weight: 600;
    }
  }

  /* 我的卡片信息 */
  .info{
    margin-top:35pt;
    width:90%;
    position: absolute;
    left:5%;
    background: #fff;
    border-radius: 6pt;
    box-shadow:0px 0px 10px rgba(167, 165, 165, 0.5);
    .bottom{
      position: relative;
      display: flex;
      margin-top: 40px;
      justify-content: space-around;
      padding-bottom:10pt;
      font-size: 13pt;
      color:#666;
    }
    .bottom::before{
      position: absolute;
      display: block;
      content:'';
      left:50%;
      height:65%;
      width:1pt;
      background: #D8D8D8;
    }
    .img{
      width:70pt;
      height:70pt;
      margin-top: -38pt;
      border:5pt solid #fff;
      border-radius: 50%;
    }
    .top{
      display: flex;
      justify-content: space-between;
      align-items: center;
      .left{
        display: flex;
        align-items: center;
        .user_name{
          color:#131415;
          font-size: 13pt;
        }
      }
    }
    .yeji{
      color:#333;
      font-size: 12pt;
      text-align: center;
      font-weight: 500;
    }
    .num{
      color:#FF4C24;
      font-size: 16pt;
      text-align: center;
      padding-top:5pt;
    }
  }
  .icon1{
    margin-right:10pt;
    padding:2pt;
    border:1pt solid #F5A623;
    color:#F5A623;
    font-size: 8pt !important;
    border-radius: 4pt;
  }

  /* 成员列表数据 */
  .list{
    margin:5%;
    background: #fff;
    padding-top:135pt;
    .item{
      border-bottom:1pt solid #DEDEDE;
      padding-top:20pt;
      .top{
        width:100%;
        display: flex;
        align-items: center;
        // justify-content: space-between;
        .left{
          display: flex;
          align-items: center;
          flex: 1;
          .name{
            display: inline-block;
            margin-right:10pt;
            font-size: 11pt;
          }
        }
        .icon{
          margin-right: 5pt;
          padding-bottom: 5pt;
          width:34pt;
          height:34pt;
          border-radius: 50%;
        }
      }
      .bottom{
        display: flex;
        // justify-content:space-around;
        margin-top: 5px;
        margin-left: 3pt;
        padding-right:20pt;
        li{
          float: left;
          text-align: center;
        }
        ul>li:nth-child(2){
          color:#FF4C24;
          font-weight: 600;
          font-size: 12pt;
          margin:5pt 0;
        }
      }
    }
  }
  .left_r{
    display: flex;
    flex-direction: row-reverse;
    padding-right:10px;
    padding-bottom: 10px;
    &>img{
      width:60px;
      height:60px;
    }
    img:nth-child(2){
      margin-right:50px;
    }
  }
</style>



